@import 'variables.scss';
@import 'reset.scss';
@import 'animate.scss';
@import 'buttons.scss';
@import 'modal.scss';

.text-muted {
  color: #6c757d !important;
}

.App {
  width: 100vw;
  height: 100vh;
  padding: var(--body-gap);
  background-image: linear-gradient(
    to right,
    var(--color-primary),
    var(--color-secondary)
  );
}

.main {
  .back-to-btn {
    font-size: 0.875rem;
    height: 28px;
    line-height: 28px;
    padding: 0 8px;
  }
  .game-module {
    padding-top: calc(1.5 * var(--body-gap));
    padding-bottom: calc(1.5 * var(--body-gap));
  }
}
